import httpUtils from 'config/index'

class rankingService {
  /**
   * 获取排行榜列表
   * @returns {*}
   */
  getList (){
    return httpUtils.get('/index/item/reco_brand_list');
  }
  /**
   * 获取十大列表
   * @returns {*}
   */
  getTenList (){
    return httpUtils.get('/index/category/brand_ranking');
  }

  /**
   * 获取推荐列表
   * @returns {*}
   */
  getRecommend(){
    return httpUtils.get('/index/item/get_reco_item_list');
  }

  /**
   * 获取精选推荐数据
   */
  getSelectReco(){
    return httpUtils.get('/index/item/selected_reco');
  }
}
export default new rankingService()



