<template>
    <div class="rankingList">
      <Search></Search>
      <div class="commonNav">
        <div class="wrapper">
          <div class="part">
            <router-link   to="/">
              <span>首页</span>
              <i></i>
            </router-link>
            <router-link to="/list" >
              <span>找项目</span>
              <img class="hot" src="../../assets/hot.png">
              <i></i>
            </router-link>
            <router-link to="/rankingList" class="active">
              <span>排行榜</span>
              <i></i>
            </router-link>
            <router-link to="/news">
              <span>行业资讯</span>
              <i></i>
            </router-link>
          </div>
        </div>
      </div>
        <div class="wrapper box1">
          <div class="box1">
            <div class="longBanners">
              <span v-for="item in adve[0]" :key="'adve'+item.id">
                  <router-link :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
                  <a :href="item.link" v-else target="_blank"><img :src="item.img"></a>
              </span>
            </div>
            <div class="ranking">
              <div class="title clearfix" >
                <span class="fl" >推荐品牌排行榜</span>
<!--                <span class="fr more"> 更多 <span class="el-icon-arrow-right"></span></span>-->
              </div>
              <div class="list">
                <router-link :to="'/detail?id='+item.id" class="item clearfix" v-for="(item,index) in list.reco_brand" :key="'ten'+item.id">
                  <div class="order fl">{{index+1}}</div>
                  <div class="name fl">{{item.brand}}</div>
                  <div class="count fr">{{item.read_num}}</div>
                </router-link>
              </div>
            </div>
            <!-- 轮播图 -->
            <div class="midMid">
              <div class="carousel">
                <el-carousel >
                  <el-carousel-item v-for="item in adve[1]" :key="'adve'+item.id">
                    <router-link class="small" :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
                    <a :href="item.link" v-else target="_blank" class="small"><img :src="item.img"></a>
                  </el-carousel-item>
                </el-carousel>
              </div>
              <div class="choice">
                <p class="title">
                  <span>精选推荐 :</span>
                  <router-link :to="'/detail?id='+item.id" v-for="item in selectReco" :key="'select'+item.id"> {{item.brand}}</router-link>
                </p>
              </div>
            </div>
            <!-- 轮播图 -->
            <div class="ranking">
              <div class="title clearfix" >
                <span class="fl" >综合品牌排行榜</span>
<!--                <span class="fr more"> 更多 <span class="el-icon-arrow-right"></span></span>-->
              </div>
              <div class="list">
                <router-link :to="'/detail?id='+item.id" class="item clearfix" v-for="(item,index) in list.brand" :key="'brand'+item.id">
                  <div class="order fl">{{index+1}}</div>
                  <div class="name fl">{{item.brand}}</div>
                  <div class="count fr">{{item.read_num}}</div>
                </router-link>
              </div>
            </div>
            <div class="longBanners">
               <span v-for="item in adve[2]" :key="'adve'+item.id">
                  <router-link :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
                  <a :href="item.link" v-else target="_blank"><img :src="item.img"></a>
              </span>
            </div>
            <div class="ranking" v-for="item in tenList" :key="'ten'+item.cate_id">
              <div class="title clearfix" >
                <span class="fl" >十大<span class="strong">{{item.cate_name}}</span>品牌排行榜</span>
<!--                <span class="fr more"> 更多 <span class="el-icon-arrow-right"></span></span>-->
              </div>
              <div class="list">
                <router-link :to="'/detail?id='+item.id"  class="item clearfix" v-for="(item,index) in item.item_data" :key="'item'+item.id">
                  <div class="order fl">{{index+1}}</div>
                  <div class="name fl">{{item.brand}}</div>
                  <div class="count fr">{{item.read_num}}</div>
                </router-link>
              </div>
            </div>

            <div class="longBanners">
              <span v-for="item in adve[3]" :key="'adve'+item.id">
                  <router-link :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
                  <a :href="item.link" v-else target="_blank"><img :src="item.img"></a>
              </span>
            </div>
          </div>
          <div class="box2 clearfix">
            <div class="recommendBox fl">
              <div class="title clearfix">
                <h2 class="fl">项目推荐</h2>
                <a class="fr" href="">更多 &gt;</a>
              </div>
              <div class="content clearfix">

                <div class="box" v-for="item in reco" :key="'reco'+item.id">
                  <router-link  :to="'/detail?id='+item.id">
                    <img :src="item.img"/>
                    <span class="name over">{{item.item_name}}</span>
                    <p class="desc clearfix">
                      <b class="fl">￥{{item.money}}万</b>
                      <span class="fr">门店: {{item.shop_count}}家</span>
                    </p>
                  </router-link>
                </div>
              </div>
            </div>
            <IndustryList class="fr industryList" ></IndustryList>
          </div>
        </div>
    </div>
</template>

<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import IndustryList from '@/components/IndustryList'
  import commonApi from "@/api/common-api"
  import api from "@/api/ranking-api"

    export default {
        name: "rankingList",
        data(){
          return{
            adve:[[],[],[],[],[]],
            list: [],
            tenList:{},
            selectReco:[],
            reco:'',
          }
        },
        mounted: function(){
          this.getData();

        },
        methods: {
          getData(){
              commonApi.getAdve({wp:5}).then((data)=>{
                this.adve = data
              });
              api.getList().then((data)=>{
                this.list = data
              })
              api.getTenList().then((data)=>{
                this.tenList = data
              })
              api.getRecommend().then(data =>{
                this.reco = data;
              })
            api.getSelectReco().then(data =>{
              this.selectReco = data;
            })
          }
        },
        components:{
          Search,
          HotOrder,
          IndustryList
        }
    }
</script>

<style  scoped lang="less" type="text/less">
    .rankingList .box1{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    .box2{
      width: 100%;
    }
    /* 轮播图 */
    .midMid{
      margin-top: 10px;
      .carousel{
        width: 596px;
        height: 314px;
        background: #fff;
        box-shadow: 0px 0px 4px 0px rgba(153,153,153,0.5);
      }
      .choice{
        height: 104px;
        width: 596px;
        padding: 18px 10px 18px 45px;
        margin-top: 10px;
        background-color: #fff;
        box-sizing: border-box;
        .title{
          display: flex;
          flex-wrap: wrap;
          height: 100%;
          line-height: 36px;
          overflow: hidden;
          font-weight: bold;
          color: #000;
          a{
            font-weight: normal;
            color: #666;
            margin-right: 12px;
            white-space: nowrap;
          }
          span{
            margin-right: 12px;
          }
        }
      }

    }
    /* 轮播图 */
    /* tabbar */
    .commonNav {
      width: 100%;
      height: 40px;
      line-height: 40px;
      background: #2F333B;
      font-size: 16px;
      color: #fff;

      .all {
        float: left;
        width: 240px;
        height: 40px;
        text-align: center;
        background: #51575D;

        img, label {
          display: inline-block;
          vertical-align: middle;
        }

        img {
          margin: -2px 14px 0 0;
        }
      }

      .part {
        a {
          float: left;
          width: 160px;
          height: 40px;
          position: relative;
          color: #fff;
          text-align: center;
          .hot{
            position: absolute;
            top: 4px;
            margin-left: 6px;
          }
        }

        a.active {
          background: #1A1D21;

          i {
            width: 34px;
            height: 2px;
            background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
            background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
            background: linear-gradient(to bottom, #579EFA, #4F74F4);
            position: absolute;
            left: 50%;
            margin-left: -17px;
            bottom: 2px;
            z-index: 1;
          }
        }
      }
    }
    /* tabbar */
    .rankingList{
      background-color: #F4F5FA;
      padding-bottom: 98px;
      .longBanners {
        margin: 20px 0;
        img{
          width: 1200px;
          height: 80px;
          display: block;
        }
      }
    }

    /* 排行榜 */
    .ranking{
      width: 292px;
      height: 430px;
      background-color: #fff;
      margin-top: 10px;
      .title{
        height: 50px;
        line-height: 50px;
        font-size: 14px;
        font-weight: bold;
        padding: 0 19px;
        background-color: #F0F7FF;
        .strong{
          color: #5389F7;
        }
        /*.more{*/
        /*  color: #999;*/
        /*  font-size: 12px;*/
        /*  font-weight: normal;*/
        /*}*/
      }
      .list{
        padding:10px 20px 23px 20px;
        .item{
          display: block;
          padding-top:14px;
          .order{
            width: 18px;
            height: 21px;
            text-align: center;
            line-height: 21px;
            font-size: 12px;
            color: #999;
            background: url("/static/image/tag_order_default.png") 100% 100% no-repeat;
          }
          .name{
            width: 65%;
            font-size: 14px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-left: 10px;
          }
          .count{
            color: #999;
            font-size: 12px;
          }
        }
        .item:nth-child(-n+3) .order{
          color: #fff;
          background: url("/static/image/tag_order.png") 100% 100% no-repeat;
        }
      }
    }
    /* 排行榜 */

  /* 项目推荐 */
    .recommendBox{
      width: 900px;
      .title{
        height: 50px;
        line-height: 50px;
        h2{
          font-size: 22px;
          font-weight: bold;
          color: #000;
        }
        a{
          color: #999;
          font-size: 14px;
        }
      }
      .content{
        width: 900px;
        margin: 0 auto;
        .box{
          float: left;
          width: 217px;
          background: #F4F5FA;
          margin: 0 10px 10px 0;
          position: relative;
          img{
            display: block;
            width: 217px;
            height: 217px;
          }
          .name{
            width: 94%;
            padding: 0 3%;
            display: block;
            height: 40px;
            line-height: 40px;
            background: rgba(0,0,0,0.4);
            color: #fff;
            position: absolute;
            left: 0;
            top: 177px;
          }
          .desc{
            padding: 0 11px 0 13px;
            height: 48px;
            line-height: 48px;
            background-color: #fff;
            b{
              font-size: 16px;
              color: #FF8A00;
              font-weight: bold;
            }
            span{
              font-size: 14px;
              color: #999;
            }
          }
        }
        .box:nth-of-type(4n+4){
            margin-right: 0;

        }
      }
    }
  /* 项目推荐 */
</style>



